import React from 'react';
import ReactDOM from 'react-dom';
import App from './app';
import installGlobalHook from './app/backend/installGlobalHook';
installGlobalHook(window);

function _inject(src, done) {
  if (!src || src === 'false') {
    done();
    return;
  }

  var script = document.createElement('script');
  script.src = src;
  script.onload = done;
  document.body.appendChild(script);
}

var config = {
  alreadyFoundReact: true,
  showHiddenThemes: true,
  inject: function inject(done) {
    _inject('https://modecity-test.oss-cn-hangzhou.aliyuncs.com/backend.js', function () {
      var wall = {
        listen: function listen(fn) {
          window.addEventListener('message', function (evt) {
            if (evt.source === window) {
              fn(evt.data);
            }
          });
        },
        send: function send(data) {
          window.postMessage(data, '*');
        }
      };
      done(wall);
    });
  }
};
var reactInspect = document.createElement('div');
reactInspect.className = 'react-inspect-menu';
reactInspect.id = 'app';
document.body.appendChild(reactInspect); // const inspectIFrame = document.createElement('iframe');
